import { IoMdCalendar } from 'react-icons/io';
import React from 'react';
import { MuiPickersUtilsProvider, TimePicker } from '@material-ui/pickers';
import DayjsUtils from '@date-io/dayjs';
import { Button } from 'shared/components/Elements';
import { Calendar } from 'react-date-range';
import dayjs from 'dayjs';
import clsx from 'clsx';
import { useAppState } from 'shared/state';
import { useScheduleDatePick } from '../hooks/useScheduleDatePick';
import { useTranslation } from 'react-i18next';
import { convertToLocalizationKey } from 'shared/services/utils/convertToLocalizationKey';
import { uz, ru, enUS, arSA } from 'date-fns/locale';
import { useTranslationHelpers } from 'shared/hooks/useTranslationHelpers';

type Props = {
	onScheduleModalCloseClicked: () => void;
};

const getLocale = (language: string) => {
	switch (language) {
		case 'UZ': {
			return uz;
		}
		case 'RU': {
			return ru;
		}
		case 'EN': {
			return enUS;
		}
		case 'AR': {
			return arSA;
		}
	}
};

export function ScheduleDateTime({ onScheduleModalCloseClicked }: Props) {
	const { isDarkMode } = useAppState();
	const { t, i18n } = useTranslation();
	const { isRTL } = useTranslationHelpers();

	const {
		openCustomDate,
		scheduleError,
		handleDateChange,
		handleTimeChange,
		handleSaveScheduleDate,
		toggleOpenCustomDate,
		handleSelectPresetDateTime,
		schedulingDate,
		selectedPresetDate,
		schedulingTime,
		SCHEDULE_PRESET_VALUES,
		selectedSchedulingDateTime
	} = useScheduleDatePick({ onScheduleModalCloseClicked });

	return (
		<MuiPickersUtilsProvider utils={DayjsUtils}>
			<div className="schedule-date__wrapper">
				{openCustomDate ? (
					<div className="schedule-custom-date__wrapper">
						<div className="schedule-custom-date__header text-xl font-semibold">
							{t('choose_date_and_time')}
						</div>
						<div className="schedule-custom-date__content flex justify-between">
							<div
								className={clsx(
									{ 'w-[100%]': isRTL, 'w-[70%]': !isRTL },
									{ 'dark-theme': isDarkMode }
								)}
							>
								<Calendar
									locale={getLocale(i18n.language)}
									classNames={{
										selected: '!text-primary-dark'
									}}
									date={schedulingDate}
									onChange={handleDateChange}
									minDate={new Date()}
								/>
							</div>
							<div className="date-content__selected pt-10">
								<div>{dayjs(schedulingDate).format('MMM D, YYYY')}</div>
								<div className="pt-4">
									<TimePicker
										variant="inline"
										autoOk
										value={dayjs().hour(schedulingTime.hour).minute(schedulingTime.minute)}
										onChange={handleTimeChange}
										inputVariant="standard"
										size="small"
										error={scheduleError?.error}
										helperText={scheduleError?.message}
									/>
								</div>
							</div>
						</div>
						<div className="schedule-custom-date__footer flex justify-end gap-x-2">
							<Button onClick={onScheduleModalCloseClicked}>{t('cancel')}</Button>
							<Button
								disabled={scheduleError?.error}
								onClick={handleSaveScheduleDate}
								classes={{ root: '!bg-primary-dark !text-white' }}
								variant="contained"
							>
								{t('schedule_send')}
							</Button>
						</div>
					</div>
				) : (
					<div className="schedule-preset-date__wrapper">
						<h3 className="schedule-preset-date-header__text flex-1 pb-5 text-xl font-semibold">
							{t('schedule_send')}
						</h3>
						<div className="schedule-preset-date__content">
							{SCHEDULE_PRESET_VALUES.filter((presetDate) => !presetDate.isHidden).map(
								(presetData, idx) => (
									<div
										key={idx}
										onClick={() => handleSelectPresetDateTime(presetData)}
										className={clsx(
											'preset-content__item flex cursor-pointer items-center justify-between py-2 text-base',
											{
												'bg-primary-dark px-1 text-white': selectedPresetDate?.id === presetData.id
											}
										)}
									>
										<div className="preset-item__title">
											{t(convertToLocalizationKey(presetData.title))}
										</div>
										<div
											className={clsx('preset-item__date text-kgrey-dark-1', {
												'!text-white': selectedPresetDate?.id === presetData.id
											})}
										>
											{dayjs(presetData.schedulingDate.toDate()).format('MMM D, h:mm A')}
										</div>
									</div>
								)
							)}
						</div>
						{scheduleError ? (
							<div className="schedule-preset-date__error w-full border-b-1px border-kred-red pb-1 text-sm text-kred-red">
								{scheduleError.message}
							</div>
						) : null}
						<div
							className={clsx('schedule-preset-date__footer border-t-1px border-kgrey py-3', {
								'mt-4': !scheduleError
							})}
						>
							<div
								onClick={toggleOpenCustomDate}
								className="footer__open-custom-date flex cursor-pointer items-center gap-x-2 hover:text-primary"
							>
								<IoMdCalendar />
								<div>{t('choose_date_and_time')}</div>
							</div>
							<div className="footer__buttons flex justify-end gap-x-2">
								<Button onClick={onScheduleModalCloseClicked}>{t('cancel')}</Button>
								<Button
									onClick={handleSaveScheduleDate}
									classes={{ root: '!bg-primary-dark !text-white' }}
									variant="contained"
									disabled={!selectedSchedulingDateTime}
								>
									{t('schedule_send')}
								</Button>
							</div>
						</div>
					</div>
				)}
			</div>
		</MuiPickersUtilsProvider>
	);
}
