import { useQueryParams } from './useQueryParams';

export function useCasesRefetch() {
	const { changeUrlParams, query } = useQueryParams();

	function refetchCurrentStatusCasesOnly(statusId: number) {
		const isChatActive = Boolean(query?.case || query?.chat);
		//Main logic to append or remove query params to re-fetch exact statuses cases
		if (query?.reloadCases) {
			changeUrlParams(
				{
					reloadStatusId: statusId
				},
				//Case 3101: we need to avoid replacing query params when case chat is open.
				//https://youtu.be/1hXeS-Cb94o issue video
				!isChatActive
			);
		} else {
			changeUrlParams(
				{
					reloadCases: true,
					reloadStatusId: statusId
				},
				!isChatActive
			);
		}
	}

	return {
		refetchCurrentStatusCasesOnly
	};
}
