/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';

import { KntAutoSizeTextField, KntInputField } from 'shared/components/Fields';
import React, { useMemo, useState } from 'react';
import { Field, FormikProvider, useFormik } from 'formik';
import * as yup from 'yup';
import { Button } from 'shared/components/Elements';
import config from 'config';
import { useAxios, useNotistack } from 'shared/hooks';
import { useParams } from 'react-router';
import { Loader } from 'shared/components';
import { LanguageSelect } from 'shared/components/HeaderKnt/components/LanguageSelect/LanguageSelect';
import { useTranslation } from 'react-i18next';

const imgPath = `${config.PUBLIC_ASSETS}img/`;

function FeedbackPage() {
	const [temporaryLanguage, setTemporaryLanguage] = useState('EN');
	const { t } = useTranslation('translation', { lng: temporaryLanguage });

	const validationSchema = useMemo(
		() =>
			yup.object({
				name: yup.string().trim().required(t('required')),
				email: yup.string().email(t('valid_email')).required(t('required')),
				title: yup.string().trim().required(t('required')),
				body: yup.string().trim().required(t('required'))
			}),
		[t]
	);

	const formik = useFormik({
		initialValues: {
			name: '',
			email: '',
			title: '',
			body: ''
		},
		validationSchema: validationSchema,
		onSubmit: async (values) => {
			console.log(values, 'feedback form');

			await sendFeedbackRequest({
				values
			});
		}
	});
	const { showNotification } = useNotistack();
	const { companyUniqId } = useParams<{ companyUniqId: string }>();

	const { fetchData: sendFeedbackRequest, loading } = useAxios({
		url: `/feedback/${companyUniqId}/`,
		method: 'post',
		dynamicValues: true,
		cb: {
			success: (data) => {
				showNotification({
					message: t('feedback_send_success')
				});

				formik.resetForm();
			},
			error: (error) => {
				console.log(error, 'feedback error');
			}
		}
	});

	const changeLanguage = (language: string) => {
		setTemporaryLanguage(language);
	};

	return (
		<FormikProvider value={formik}>
			<div
				dir={temporaryLanguage === 'AR' ? 'rtl' : 'ltr'}
				css={css`
					background-image: url(${imgPath}bg-welcome.jpg);
				`}
				className="feedback-page__wrapper min-h-full bg-cover bg-no-repeat"
			>
				<div className="flex-center-x mx-2 items-center sm:mx-0">
					<div className="w-full md:w-auto">
						<div className="mt-6 sm:-ml-2.5 ">
							<LanguageSelect
								isRegistering={false}
								customLanguageSelect={{
									changeLanguage,
									language: temporaryLanguage
								}}
							/>
						</div>

						<form
							className="mt-20 w-full rounded bg-white px-5 py-3.5 shadow-2xl md:w-[28rem]"
							onSubmit={formik.handleSubmit}
						>
							<h3 className="mt-2.5 mb-3.5 font-semibold text-kdark-424242">
								{t('question_and_feedback')}
							</h3>
							<Field
								inputElementClassNames="dark:bg-kgrey-light-grey-1 dark:placeholder-kgrey"
								type="text"
								label={t('full_name')}
								name="name"
								component={KntInputField}
								placeholder={t('enter_full_name')}
								required
							/>
							<Field
								inputElementClassNames="dark:bg-kgrey-light-grey-1 dark:placeholder-kgrey"
								type="email"
								label={t('email_address')}
								name="email"
								component={KntInputField}
								placeholder={t('enter_email_address')}
								required
							/>
							<Field
								inputElementClassNames="dark:bg-kgrey-light-grey-1 dark:placeholder-kgrey"
								name="title"
								component={KntAutoSizeTextField}
								label={t('message_title')}
								required
								type="text"
								onChangeHandler={formik.handleChange}
							/>
							<Field
								inputElementClassNames="dark:bg-kgrey-light-grey-1 dark:placeholder-kgrey"
								name="body"
								component={KntAutoSizeTextField}
								label={t('message')}
								required
								type="textarea"
								onChangeHandler={formik.handleChange}
							/>

							<div className="feedback-form__button flex justify-end">
								<Button
									variant={'contained'}
									className={'h-8 w-fit !bg-primary-dark text-right !font-sans !text-white'}
									type="submit"
								>
									{loading ? <Loader tiled size={24} /> : t('send_message')}
								</Button>
							</div>
						</form>
					</div>
				</div>
			</div>
		</FormikProvider>
	);
}

export default FeedbackPage;
