import React from 'react';
import { BiSearch } from 'react-icons/bi';

export function SwitchBusinessSearch({
	query,
	handleSearch,
	placeholder
}: {
	query: string;
	handleSearch: (event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => void;
	placeholder?: string;
}) {
	return (
		<div className={'flex justify-end'}>
			<div className={'relative inline-block'}>
				<div className="absolute left-1 top-1/2 -translate-y-2/4 text-2xl text-[#A5A7B2]">
					<BiSearch />
				</div>
				<input
					value={query}
					onChange={handleSearch}
					type="search"
					className={
						'w-[200px] rounded-2xl py-2 pr-4 pl-8 focus:outline-none focus:ring-2 focus:ring-primary-300 dark:bg-kdark dark:text-white dark:focus:ring-primary'
					}
					placeholder={placeholder || 'Search'}
				/>
			</div>
		</div>
	);
}
